import EventListener from "./eventListener";

//
export default class EventNotify {
    batchNo : number;
    type : number;
    px : number;
    py : number;
    sourceEvent : any;
    listener : EventListener;

    constructor() {
    }

    /**
     * draw
     */
    set(obj : any){
        this.batchNo = obj.batchNo;//
        this.type = obj.type;//12
        this.px = obj.px;
        this.py = obj.py;
        this.sourceEvent = obj.sourceEvent;
        this.listener = obj.listener;
    }
}


