import Map from "../ui/components/game/map";
import MPromise from "../util/promise";
import Component from "../ui/components/Component";
import Thread from "../util/thread";
import CollisionDetector from "../collision/collisionDetector";

/**
 * 
 */
export default class BoxCollisionDetector implements CollisionDetector{
    constructor() {
    }

    /**
     * 
     *
     * @return 0[]
     */
    thread_detectCollision(e : any){
        let data = JSON.parse(e.data);
        let mapData = data.mapData;
        let mapColMin = data.mapColMin;
        let mapColMax = data.mapColMax;
        let mapRowMin = data.mapRowMin;
        let mapRowMax = data.mapRowMax;
        let collision = [];//

        let minRow;//
        let minCol;//
        let maxRow;//
        let maxCol;//
        for (let row = mapRowMin; row <= mapRowMax; row++) {
            for (let col = mapColMin; col <= mapColMax; col++) {
                if (mapData[row] && mapData[row][col] && mapData[row][col].block) {
                    minRow = !minRow ? row : Math.min(row, minRow);
                    minCol = !minCol ? col : Math.min(col, minCol);
                    maxRow = !maxRow ? row : Math.max(row, maxRow);
                    maxCol = !maxCol ? col : Math.max(col, maxCol);
                }
            }
        }
        if (minRow && maxRow && minCol && maxCol)
        {
            collision.push(minRow);
            collision.push(maxRow);
            collision.push(minCol);
            collision.push(maxCol);
        }

        if (collision.length > 0)
        {
            (<any>self).postMessage(JSON.stringify(collision));
        }
        else
        {
            (<any>self).postMessage(0);
        }
    }

    /**
     * this.parentMap
     *
     * @param com 
     * @param sx x
     * @param sy y
     * @param thread 
     * @param fixCoor 
     * @return rejectresolve:
     */
    detectCollision(com : Component, sx : number, sy : number, thread : Thread, fixCoor? : boolean) : MPromise
    {
        let $this = com;
        let promise = new MPromise();
        if (!($this.parent instanceof Map))
        {
            promise.resolve();
            return promise;
        }
        if (sx > $this.parent.getWidth() || sy > $this.parent.getHeight()
            || sx + $this.getWidth() < 0 || sy + $this.getHeight() < 0)//map
        {
            promise.resolve();
            return promise;
        }
        //
        if ($this.parent.mapSize == 0 || !$this.parent.mapData || $this.parent.mapData.length === 0)
        {
            promise.reject();
            return promise;
        }

        let mapColMin = Math.floor(sx / $this.parent.mapSize);
        let mapColMax = Math.floor((sx + $this.getWidth()) / $this.parent.mapSize);
        if ((sx + $this.getWidth()) % $this.parent.mapSize === 0)
        {
            mapColMax--;
        }
        let mapRowMin = Math.floor(sy / $this.parent.mapSize);
        let mapRowMax = Math.floor((sy + $this.getHeight()) / $this.parent.mapSize);
        if ((sy + $this.getHeight()) % $this.parent.mapSize === 0)
        {
            mapRowMax--;
        }
        thread.run({
            mapColMin : mapColMin,
            mapColMax : mapColMax,
            mapRowMin : mapRowMin,
            mapRowMax : mapRowMax,
            mapData : $this.parent.mapData
        },function(key, value) {
            if (key === 'parent' || key === 'controller') {
                return undefined;
            }
            return value;
        }).then((data)=>{
            if (typeof(data) === "number" && data === 0)//
            {
                promise.resolve();
            }
            else
            {
                //
                if (fixCoor)
                {
                    let collisionsArr = JSON.parse(data);
                    let minRow = collisionsArr[0];
                    let maxRow = collisionsArr[1];
                    let minCol = collisionsArr[2];
                    let maxCol = collisionsArr[3];
                    if (maxRow - minRow > maxCol - minCol)//
                    {
                        if (mapColMax - minCol > minCol - mapColMin)//
                        {
                            $this.setX(maxCol * (<Map>$this.parent).mapSize + parseInt((<Map>$this.parent).mapSize.toString()));
                        }
                        else
                        {
                            $this.setX(minCol * (<Map>$this.parent).mapSize - $this.getWidth());
                        }
                    }
                    else//
                    {
                        if (mapRowMax - minRow < minRow - mapRowMin)//
                        {
                            $this.setY(minRow * (<Map>$this.parent).mapSize - $this.getHeight());
                        }
                        else
                        {
                            $this.setY(maxRow * (<Map>$this.parent).mapSize + parseInt((<Map>$this.parent).mapSize.toString()));
                        }
                    }
                }
                promise.reject(data);
            }
        });
        return promise;
    }
}


